export default {
	props: {
		label: {
			type: String,
			default: ''
		},
		placeholder: {
			type: String,
			default: ''
		},
		status: {
			type: String,
			default: 'default'
		},
		value: {
			type: String,
			default: undefined
		},
		type: {
			type: String,
			default: 'text'
		}
	},
	methods: {
		onInput(e) {
			this.$emit('input',e.target.value)
		}
	}
}
